.swal2-container {
  z-index: 100000 !important;
}

.swal2-container .swal2-content, .swal2-container .swal2-title {
  margin-bottom: 30px;
}

.swal2-container .swal2-content input[type=text] {
  padding: 6px;
  margin: 0;
  border-radius: 4px;
  height: 30px;
  color: var(--atum-text-color-var1);
  border: 1px solid var(--gray-200);
  box-shadow: none;
}

.swal2-container .swal2-content input[type=text]:hover {
  border-color: var(--atum-text-color-var1);
}

.swal2-container .swal2-content input[type=text]:focus {
  border-color: var(--primary);
}

.swal2-container .swal2-content .atum-link {
  text-decoration: underline;
}

.swal2-container .swal2-title {
  line-height: 1.3;
  font-size: 23px;
  color: var(--atum-text-color-var1);
}

.swal2-container .swal2-modal {
  font-family: inherit;
}

.swal2-container .swal2-modal .swal2-close {
  font-weight: 100;
  font-family: inherit;
  font-size: 30px;
  line-height: 30px;
  color: var(--gray-500);
}

.swal2-container .swal2-modal p {
  font-size: 15px;
  text-align: center;
}

.swal2-container .swal2-modal button, .swal2-container .swal2-modal a {
  transition: background-color 0.2s ease-in-out;
}

.swal2-container .swal2-modal button:focus, .swal2-container .swal2-modal a:focus {
  outline: none;
  box-shadow: none;
}

.swal2-container .swal2-modal button {
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 400;
  padding: 9px 19px;
  font-size: 13px;
  letter-spacing: 1px;
}

.swal2-container .swal2-modal button.btn-sm {
  padding: 0.25rem 0.5rem;
  font-size: 12px;
}

.swal2-container .swal2-modal button .atum-icon {
  position: relative;
  font-size: 1.15em;
  top: .15em;
  padding-right: 3px;
}

.swal2-container .swal2-modal a {
  text-decoration: none;
  color: var(--primary) !important;
}

.swal2-container .swal2-modal a:hover {
  color: var(--wp-link-hover);
}

.swal2-container .atum-loading {
  width: 18px;
  height: 18px;
  border: 3px solid rgba(0, 184, 219, 0.25);
  border-top-color: #00B8DB;
  border-radius: 50%;
  position: absolute;
  z-index: 50;
  animation: loader-rotate 1s linear infinite;
  top: 30px;
  left: 0;
  right: 0;
  margin: auto;
  width: 40px;
  height: 40px;
  border-width: 3px;
}

@keyframes loader-rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#wpfooter {
  padding-top: 0;
  display: none;
}

#wpfooter .footer-box {
  height: 90px;
  border-radius: 5px;
  background-color: var(--white);
  box-shadow: 0 4px 0 0 var(--gray-200);
  background: var(--atum-expanded-bg) url("../images/stock-central/bg-footer.png") no-repeat center 81%;
  background-size: cover;
  padding: 0 23px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#wpfooter .footer-box.no-style {
  background-image: none;
  background-color: transparent;
  box-shadow: none;
  padding: 0;
  height: auto;
}

#wpfooter .footer-box.no-style .footer-atum-logo, #wpfooter .footer-box.no-style .footer-atum-buttons, #wpfooter .footer-box.no-style #footer-left, #wpfooter .footer-box.no-style .clear {
  display: none;
}

#wpfooter .footer-box .footer-atum-logo {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 6px 0 5px;
}

#wpfooter .footer-box .footer-atum-logo img {
  width: 34px;
}

#wpfooter .footer-box .footer-atum-logo span {
  color: var(--atum-footer-title);
  font-size: 27px;
  margin-left: 10px;
  font-weight: bold;
}

#wpfooter .footer-box .footer-atum-text {
  font-size: 12px;
